import React, { useEffect, useRef, useState } from 'react';
import { postList } from './Data';
import "../../css/Board/PostDetail.css";
import ReactHtmlParser from 'react-html-parser';

const PostDetail = ({ history, location, match }) => {
  const [ data, setData ] = useState({});
  const { no } = match.params;
  const setPostList = useRef(postList);

  useEffect(() => {
    if(localStorage.getItem('data')){
      setPostList.current = JSON.parse(localStorage.getItem('data'));
    }

    // eslint-disable-next-line eqeqeq
    const array = setPostList.current.filter(x => x.no == no);
    if (array.length === 1) {
      setData(array[0]);
    }
  }, [ no ]);

  const onDeletePost = () =>{
    const newData = setPostList.current.filter(item => item.no != no);
    console.log(newData);
    localStorage.setItem("data", JSON.stringify(newData));
    history.goBack();
  }

  return (
    <section className="detail-section">
      <div>Í≤åÏãúÍ∏Ä ÏÉÅÏÑ∏Ï†ïÎ≥¥</div>

      <div className="detail-container">
        {
          data ? (
            <>
              <div className="detail-row">
                <label>Í≤åÏãúÍ∏Ä Î≤àÌò∏</label>
                <div>{ data.no }</div>
              </div>
              <div className="detail-row">
                <label>Ï†úÎ™©</label>
                <div>{ data.title }</div>
              </div>
              <div className="detail-row">
                <label>ÏûëÏÑ±Ïùº</label>
                <div>{ data.createDate }</div>
              </div>
              <div className="detail-row">
                <label>Ï°∞ÌöåÏàò</label>
                <div>{ data.readCount }</div>
              </div>
              <div className="detail-row">
                <label>ÎÇ¥Ïö©</label>
              </div>
              <div className='detail-content'>
                  { ReactHtmlParser(data.content) }
              </div>
            </>
          ) : 'Ìï¥Îãπ Í≤åÏãúÍ∏ÄÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.'
        }
        <button className="detail-list-btn" onClick={() => history.goBack()}>Î™©Î°ùÏúºÎ°ú ÎèåÏïÑÍ∞ÄÍ∏∞</button>
        <button className="post-delete-btn" onClick={() => onDeletePost()}>ÏÇ≠Ï†ú</button>
      </div>
    </section>
  )
}

export default PostDetail;