import React, {useState} from 'react'
import "../css/Search/Search.css"
import { Link } from 'react-router-dom';

export default function Search({history}) {
    const [query, setQuery] = useState("");
    return (
        <div className='search-wrap'>
            <h2>Ï±Ö Ï†ïÎ≥¥ Í≤ÄÏÉâ</h2>
            <section className="search-container">
                <input onKeyPress={e => {if(e.key==='Enter') history.push(`../SearchResult?title=${e.target.value.replace(" ", "+")}`);}} onChange={(e) => {setQuery(e.target.value);}} className="book-input" type='text' placeholder='Ï±Ö Ï†úÎ™©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî'/>
                {
                    query === "" ?
                    <button onClick={() => {
                        if(query === ""){
                            alert("Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
                        }
                    }} className="search-btn">Í≤ÄÏÉâ</button>
                    : <Link to={`/SearchResult?title=${query.replace(" ", "+")}`}><button className="search-btn">Í≤ÄÏÉâ</button></Link>
                }
            </section>
        </div>
      );
}
